@mixin respond($breakpoint) { 
    @if $breakpoint == phone {
       @media only screen and (max-width: 37.5em) { @content };   //600px
    }
    @if $breakpoint == tab-port {
       @media only screen and (max-width: 56.25em) { @content };    //900px
    }
    @if $breakpoint == tab-land {
       @media only screen and (max-width: 75em) { @content };    //1200px
    }
    @if $breakpoint == big-desktop {
       @media only screen and (min-width: 112.5em) { @content };    //1800px
    }
}

.mywork {
   font-family: 'Montserrat', sans-serif;

   &__header {
      text-align: center;

      &--title {
         font-size: 40px;
         color: #333;
         font-weight: 200;
         margin: 60px 50px 20px 50px;
         font-family: 'Open Sans',sans-serif;
         letter-spacing: 1.8px;

         &:after {
            content: ""; /* This is necessary for the pseudo element to work. */ 
            display: block; /* This will put the pseudo element on its own line. */
            margin: 0 auto; /* This will center the border. */
            width: 100px; /* Change this to whatever width you want. */
            padding-top: 17px; /* This creates some space between the element and the border. */
            border-bottom: 3.4px solid orange; /* This creates the border. Replace black with whatever color you want. */
        }

         @include respond(tab-port) {
            font-weight: 400;
            font-size: 30px;
            margin: 35px;

            &:after {
               width: 80px;
               padding-top: 14px;
            }
         }

         @include respond(phone) {
            font-weight: 500;
            font-size: 22px;
            margin: 30px;

            &:after {
               width: 60px;
               padding-top: 13px;
            }
         }
      }

      &--subtitle {
         font-size: 18px;
         margin-top: 30px;
         margin-bottom: 35px;
         color: #202e78;
      }
   }

   &__content {
      width: 100%;
      display: flex;
      flex-wrap: wrap;
      flex-direction: row;
      justify-content: center;
      padding: 0 60px;

      @include respond(tab-port) {
         padding: 0 30px;
      }
   }
}